import { useCallback } from 'react';
import { useAssistantEffect } from './useAssistantEffect';

export const ASSISTANT_HUB_SPI_IMPLEMENTOR =
  '5e9a9fda-5d98-4339-8ebd-d7721e790b67';

type ToolOutput = {
  content: string;
  success: boolean;
};

/**
 * `useToolSideEffect()` is a hook that allows you to listen for assistant tool (aka skill tool) execution side effects.
 * It will be triggered only for tools defined in the assistant hub.
 * If you want to listen your own side effects triggered in your own SPI Assistant, use `useAssistantEffect()` instead.
 */
export function useToolSideEffect<ToolInput>(
  handler: (toolInput: ToolInput, toolOutput: ToolOutput) => void,
  toolName: string,
) {
  const wrappedHandler = useCallback(
    (payload: {
      toolName: string;
      request: ToolInput;
      response: ToolOutput;
    }) => {
      if (payload.toolName === toolName) {
        handler(payload.request, payload.response);
      }
    },
    [handler, toolName],
  );

  useAssistantEffect(
    wrappedHandler,
    ASSISTANT_HUB_SPI_IMPLEMENTOR,
    'TOOL_EXECUTION',
  );
}
